<script>
import $ from 'jquery'
import common from '@/common'
import Vue from 'vue'
import vueCookies from "vue-cookies"

Vue.use(vueCookies);
Vue.$cookies.config("1d"); //글로벌세팅 만료 1일
export default {
  components:{
  },
  data() {
    return {
    NoticeDialog: false,
    rememberPopup: false,
    }
  },
  methods: {    
    setInfomation() {
      this.$alert(common.messages.wait, '', 'info').then(() => {  })
    },
    setOrder() {
      this.$router.push("/order");
    },
    setShipping() {
      var loginEmail = this.$session.get("loginEmail");
      if(loginEmail == "gemglovis@gemplatforms.com") { //Glovis 분기처리
        this.$router.push("/glovis-shipping");
      } else {
        this.$router.push("/shipping");
      }
    },
    setEevenue() {
      this.$alert(common.messages.wait, '', 'info').then(() => {  })
    },
    setWms() {
      this.$alert(common.messages.wait, '', 'info').then(() => {  })
    },
    setFulfilment() {
      this.$alert(common.messages.wait, '', 'info').then(() => {  })
    },
    setReport() {
      this.$router.push("/report-weight-list");
    },
    setSellerMaster() {
        this.$router.push("/seller-master");
    },
    // 공지팝업 1일 안열기
    setPopupCancel() {
      if(this.rememberPopup){
        this.$cookies.set("rememberPopup", "Y");
      }else{
        this.$cookies.set("rememberPopup", "N");
      }
      this.NoticeDialog = false;
    },
  },
  mounted() {
    // 테스트 로그 확인
    // console.log("1.NoticeDialog "+this.NoticeDialog)
    // console.log("2.rememberPopup "+this.$cookies.get("rememberPopup"));
    // 지정쿠키 지우기
    // this.$cookies.remove("rememberPopup");
    // 공지팝업 1일 안열기
    if(this.$cookies.get("rememberPopup") == "Y"){
      this.NoticeDialog = false;
    }

    if(this.$session.get('loginId') == '' || this.$session.get('authorization') == '') {
      this.$alert(common.messages.login, '', 'info').then(() => { this.$router.push('/') })
      return false;
    }
    $(document).prop("title", "GEM ServiceMap");
    $("#bar_header").attr("style", "background-color:#E2E1F1");
    $("#top_header").show();
    $("#topNaviText").text("");

    $("#orderDaily").text(12);
    $("#orderWeekly").text(33);
    $("#orderMonthly").text(152);

    $("#shippingDaily").text(3);
    $("#shippingWeekly").text(41);
    $("#shippingMonthly").text(143);
    $("#shippingTotal").text(189);

    if(this.$session.get('loginId') == '' || this.$session.get('authorization') == '') {
      this.$alert(common.messages.login, '', 'info').then(() => {  })
      return false;
    }    
  },
